.input-row {
    display: grid;
    text-align: end;
    margin: 0 0 0.25em 0;
}

.input-row.user {
    grid-template-columns: 7em 10em 1fr;
}

.input-row.login {
    grid-template-columns: 3.5em 10em 1fr;
}

.input-row > input {
    margin-left: 1em;
}

.input-hint {
    font-size: 0.75em;
    text-align: start;
    margin-left: 0.5em;
}

.button-container {
    margin: 0.5em 0 0 0;
    display: flex;
    column-gap: 1em;
}

.button-container.user {
    margin-left: 7.5em;
}

.button-container.login {
    margin-left: 4em;
}